.social {
  display: flex;
  justify-content: center;
  padding-top: 25px;
  padding-bottom: 28px;
}

.social__list {
  display: flex;
  min-width: 142px;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social__link {
  display: block;
  width: 28px;
  height: 22px;
}
